.canva-container {
  width: 450px;
  height: 450px;
}

textarea[name="draw-script"],
textarea[name="console-script"] {
  font-family: consolas;
  resize: none;
  border-radius: 8px;
  background: #11151a;
  border: unset;
  padding: 10px;
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  min-height: 150px;
  min-width: 450px;
}

textarea[name="draw-script"]::-webkit-scrollbar,
textarea[name="console-script"]::-webkit-scrollbar {
  width: 15px;
  margin-right: 5px;
  border-radius: 100px !important;
}

textarea[name="draw-script"]::-webkit-scrollbar-thumb,
textarea[name="console-script"]::-webkit-scrollbar-thumb {
  width: 15px;
  background-color: var(--color-dark-bg);
  border-radius: 100px !important;
  cursor: pointer;
  border: 5px solid #11151a;
}

textarea[name="draw-script"]::-webkit-scrollbar-thumb:hover,
textarea[name="console-script"]::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-blue);
}

.bg_black {
  background-color: #11151a;
}

.rapid-tool {
  padding-bottom: 25px;
  border-bottom: 1px solid lightgray;
}

i.pointer {
  transform-origin: top left;
  position: absolute;
}
.mini-cta {
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  text-transform: uppercase;
}
.hidden {
  display: none !important;
}

.speed-input {
  padding: 0px 20px;
  background-color: var(--color-dark-bg);
  border-radius: 8px;
  margin: 15px;
  bottom: 0;
  right: 0;
}

.file-input {
  background-color: var(--color-dark-bg);
  border-radius: 8px;
}

.import-container {
  padding: 25px 10px;
  background-color: var(--color-normal-bg);
  border-radius: 8px;
  position: relative;
  overflow: hidden;
}

#viewDraft {
  border-radius: 8px;
}

.table-list li.no-hover.active-row:hover > div {
  background-color: var(--color-dark-bg);
}
